	module.exports = {"data":{"site":{"siteMetadata":{"title":"weiran.co","author":"Weiran Zhang"}},"markdownRemark":{"id":"/Users/weiran/git/weiran/weiran.co/src/pages/blog/2012/12/stock-android-is-far-from-perfect/index.md absPath of file >>> MarkdownRemark","html":"<p>The worst UI “feature” in Android is it’s back button. It feels like a superfluous addition from the days when Android was more like Blackberry. iOS lets apps display their own back button in their UI, and results in a much less confusing experience for the user. The problem is, can Android handle the removal of all the permanent buttons, especially the back and menu button, when so many apps depend on them?</p>\n<p>(<a href=\"http://daringfireball.net/linked/2012/12/05/android-back-button\">Via Daring Fireball</a>)</p>","frontmatter":{"title":"Stock Android is far from perfect","date":"December 06, 2012","passthroughUrl":"http://www.androidpolice.com/2012/09/18/ux-things-i-hate-about-android/"}}},"pathContext":{"slug":"/blog/2012/12/stock-android-is-far-from-perfect/","previous":{"fields":{"slug":"/blog/2012/12/latest-webkit-build-doubles-scrolling-performance-on-macbook-pro-with-retina-display/"},"frontmatter":{"title":"Latest WebKit build doubles scrolling performance on MacBook Pro with Retina Display","passthroughUrl":"http://www.anandtech.com/show/6495/latest-webkit-build-doubles-scrolling-performance-on-macbook-pro-with-retina-display"}},"next":{"fields":{"slug":"/blog/2012/12/changes-to-google-apps-or-how-we-removed-the-free-option/"},"frontmatter":{"title":"Changes to Google Apps (or how we removed the free option)","passthroughUrl":"http://googleenterprise.blogspot.co.uk/2012/12/changes-to-google-apps-for-businesses.html"}}}}

